import React, { useState } from 'react'
import { 
  Container ,
  Grid,
  Typography,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  Button
} from'@mui/material'

export const CardFilm = ({film}) => {
    const [hover,setHover] = useState(false);
  return (
    <Card sx={{ maxWidth: 345,height:300 ,background:"#000",margin:1}} onMouseMove={()=>{
       setHover(true);
    }}
    onMouseOver={()=>{
      setHover(false);
    }}
    
    >
              <CardMedia
                  component="img"
                  height="300"
                  image={film.img}
                  alt="green iguana"
                  style={{
                      objectFit:'contain'
                  }}
              />
              <CardContent  >
              
                 
                  <Typography variant="sub" color="text.secondary">
                    {film.titledate}
                  </Typography>
              </CardContent>
              <CardActions>
                  <Button size="small">Share</Button>
                  <Button size="small">Learn More</Button>
              </CardActions>
       </Card>
  )
}
